@import url(https://fonts.googleapis.com/css?family=Poppins);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  color : rgba(45,45,45,0.98);
  color : var(--color-textcolor);
  background-color: white;
  background-color: var(--color-background);
  font-family: Montserrat;
}

html{
  height: 100%;
  /*added for outline none in mobile devices*/
  -webkit-tap-highlight-color: transparent;
}

body::-webkit-scrollbar {
  width: 1em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0px rgba(45,45,45,0.98);
  -webkit-box-shadow: inset 0 0 0px var(--color-textcolor);
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(45,45,45,0.98);
  background-color: var(--color-textcolor);
  outline: 0px solid white;
  outline: 0px solid var(--color-background);
  border-radius: 50px;
}

:root {
  --color-background:white;
  --color-textcolor: rgba(45,45,45,0.98);
  --color-theme: rgba(11, 122, 231, 1);
  --color-gradient:#73baf3;
}

body{
  height:100%;
  margin: 0px;
  padding: 0px;
}
.navbar{
    width:100%;
    background-color: var(--color-background) !important;
    height: 50px;
    top:0;
    left:0;
}

.navbar_comp{
    height:100%;
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.icon_holder{
    float: right;
    margin-left: auto;
}

.ext_icon{
    width:40px;
    height:40px;
    cursor: pointer;
}
.toggler{
    width:3%;
    height:50%;
    display: flex;
    flex-direction: column;
    border: none;
    cursor: pointer;
    background: transparent;
    justify-content: space-around;
    box-sizing: border-box;
    padding: 0px !important;
    outline: none;
    margin-left: 5px;
}
.toggle_line{
    width: 30px;
    height: 2px;
    background: var(--color-theme);
}
.slider{
    height: 100%;
    background: var(--color-background) !important;
    position: fixed;
    top:0;
    left:0;
    width:75%;
    max-width: 250px;
    display: flex;
    flex-direction: column;
    z-index: 2;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.image_holder{
    height: 100%;
    max-height: 200px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.profile_image{
    height: 100%;
    max-height:200px;
    border-radius: 78%;
    padding: 10px 10% 10px 10%;
}

.nav_content{
    width:100%;
    height: 100%;
}

.nav_button{
    width:100%;
    height:40px;
    background:transparent;
    border: none;
    margin-bottom: 5px;
    cursor: pointer;
    outline: none;
    text-align: left;
    padding-left: 40%;
}

.nav_button::first-letter{
    text-transform: uppercase;
}

.nav_button:hover{
    background:var(--color-gradient);
}

.style-ul{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    padding: 0px;
}

.li-styl{
    display: flex;
    padding-left: 35%
}

.slider.open{
    left:0;
    height: 100%;
    box-shadow: 2px 0px 5px black; 
    -webkit-transform: translateX(0); 
            transform: translateX(0);
}

.loadingIndicator{
    width: 25%;
    height: 25%;
    max-width: 75px;
    max-height: 75px;
    overflow: auto;
    margin: auto;
    position: absolute;
    top: 10%; left: 0; bottom: 0; right: 0;
}
.backdrop{
    width:100%;
    height:100%;
    top:0;
    left:0;
    z-index: 1;
    position: fixed;
    background: linear-gradient(45deg, var(--color-gradient), transparent);
}
.container{
    margin: 2% 15% 2% 15%;
    width: 70%;
}

.cardContainer{
    box-shadow: 0 4px 4px 0 var(--color-textcolor);
    -webkit-transition: 0.3ms;
    transition: 0.3ms;
    margin-bottom: 20px;
    min-height: 100px;
    cursor: pointer;
    background-color: var(--color-textcolor);
    background: var(--color-background);
    padding: 10px 10px 10px 10px;
}

.cardText{
    word-wrap: break-word;
}

summary{
    color:var(--color-theme)
}

.cardTextHeader{
    font-family: Poppins;
    outline: none
}

.cardTextContent{
    font-family: Lato
}

.cardContainer:hover{
    box-shadow: 2px 4px 6px var(--color-theme);
}
.container{
    margin: 2% 15% 2% 6%;
    width: 80%;
}

.border-btm{
    border-bottom: 1px solid var(--color-theme);
    padding-bottom: 2%
}

.line-1_7{
    line-height: 1.7em
}
.header_resume{
    padding: 0px 0px 2% 0px;
    margin:2% 6%;
    display: flow-root;
    font-family: Montserrat;
    border-bottom: 1px solid var(--color-theme);
}

@media(max-width: 600px) {
    .header_resume{
        display: flex;
    }

    .sectionTitle{
        width: 100% !important;
    }

    .sectionContent{
        width:100% !important;
    }
}

.name-headr{
    color: var(--color-theme);
    font-size: 2em;
    font-weight: 400;
    margin-bottom: 1%;
}

.flt-right ul{
    list-style-type: none;
    font-size: 1em;
    margin-top: 10%;
    font-weight: 600;
}

.flt-right ul li{
    margin-bottom: 10px;
}

.section-container ul{
    padding: 0px;
    list-style-type: none;
}

.section-container ul li{
    margin-bottom: 10px;
}

.flt-right{
    margin-top: 2%;
    float: right
}

.main-inline{
    display:inline-block;
}

.section-container{
    padding-bottom: 2%;
    display:inline-block;
}

.margin-section{
    margin: 0% 6%;
    display:inline-block;
}

.sectionTitle{
    color: var(--color-theme);
    float: left;
    width:30%;
    font-family: Montserrat;
    text-transform: uppercase;
    word-wrap: break-word;
}

.sectionContent{
    float: right;
    font-family: Poppins;
    width:70%
}

.sectionContent p{
    line-height: 28px
}

.flt-left{
    float: left
}
